:global .chart-container {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
}
@font-face {
  font-family: "ds-digii";
  src: url('./fonts/DS-DIGII.TTF');
}
:global html {
  background-image: url('../assets/img/bg.110420cf.png');
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
:global body {
  margin: 0;
  padding: 0;
  background-color: rgba(40, 44, 52, 0.3) !important;
  color: #fff;
}
::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}
/*滑块*/
::-webkit-scrollbar-thumb {
  background-color: #13244c;
  border-radius: 5px;
}
::-webkit-scrollbar-thumb:hover {
  background-color: #777;
}
/*滑道*/
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px #333;
  border-radius: 5px;
}
:global .ant-table-wrapper {
  color: inherit;
}
:global .ant-table-wrapper .ant-table {
  color: inherit;
  background-color: transparent;
}
:global .ant-table-wrapper .ant-table .ant-table-thead th {
  color: #aaa;
  background-color: rgba(200, 200, 200, 0.2);
}
:global .ant-table-wrapper .ant-table .ant-table-cell:hover {
  background-color: #212c50 !important;
}
:global .ant-table-wrapper .ant-table tr.ant-table-row:hover {
  background-color: #212c50 !important;
}
:global .ant-table-wrapper .ant-table tr.ant-table-row:hover > td {
  background-color: #212c50 !important;
}
:global .ant-empty-normal {
  color: inherit;
}
:global .ant-picker {
  background-color: rgba(0, 0, 0, 0.5);
  color: #fff;
}
:global .ant-picker .ant-picker-input * {
  color: inherit;
}
:global .ant-picker-panel-container * {
  background-color: #0d142a;
  color: #fff;
}
:global .ant-picker-panel-container * .ant-picker-cell-disabled::before {
  background-color: rgba(200, 200, 200, 0.3);
  color: #aaa;
}
:global .ant-list * {
  color: #fff;
}
:global .ant-list .ant-list-item-meta-title {
  color: inherit;
}
:global .ant-list .ant-list-item-meta-description {
  color: rgba(200, 200, 200, 0.8);
}
:global .ant-collapse {
  background-color: transparent;
}
:global .ant-collapse * {
  color: #fff;
}
:global .ant-collapse .ant-collapse-header {
  color: inherit !important;
}
:global .ant-picker-input:hover .ant-picker-clear {
  background-color: transparent;
  color: #aaa;
}
:global .ant-select-selector {
  background-color: #0d142a !important;
  color: #fff !important;
}
:global .ant-select-dropdown {
  background-color: #0d142a !important;
  color: #fff !important;
}
:global .ant-select-dropdown .ant-select-item {
  color: inherit;
}
:global .ant-select-dropdown .ant-select-item.ant-select-item-option-selected:not(.ant-select-item-option-disabled) {
  background-color: #212c50;
}
:global .ant-select-dropdown .ant-select-item.ant-select-item-option-active {
  background-color: #212c50;
}
:global .border-padding-1 > .border-box-content {
  padding: 14px;
}
:global .border-padding-2 > .border-box-content {
  padding: 16.8px;
}
:global .border-padding-3 > .border-box-content {
  padding: 21px;
}
:global .border-padding-4 > .border-box-content {
  padding: 23.8px;
}
:global .border-padding-5 > .border-box-content {
  padding: 28px;
}
